







































import { Component, Vue } from 'vue-property-decorator'
import { routes } from './router'

interface route {
  path: string
  name: string
  meta?: {
    title: string
    category: string
    classes: string
  }
  redirect?: any
  component?: Vue.Component | Function
}

interface nav {
  [category: string]: {
    [classes: string]: route[]
  }
}

@Component({
  filters: {
    camel (val: string): string {
      return typeof val === 'string'
        ? val.charAt(0).toLocaleUpperCase() + val.slice(1)
        : ''
    }
  }
})
export default class App extends Vue {
  routeData: route[] = routes.slice(2)

  get navData (): nav {
    let nav: nav = {}

    routes.forEach(route => {
      if (route && route.meta) {
        let { category, classes } = route.meta

        if (!nav[category]) {
          nav[category] = {}
        }
        if (!nav[category][classes]) {
          nav[category][classes] = []
        }

        nav[category][classes].push(route)
      }
    })
    return nav
  }

  get currentRouteIndex (): number {
    let { routeData, $route } = this
    let { name } = $route
    let result = -1

    for (let i = 0, len = routeData.length; i < len; i++) {
      if (routeData[i].name === name) {
        result = i
        break
      }
    }

    return result
  }

  mounted () {
    let { main }: any = this.$refs

    this.$router.afterEach(() => {
      main.setScrollTop(0, true)
    })
  }
}
